.copyright .company{
    height: auto;
    width: 100%;
    display: flex;
    align-items: center;
    background-color: var(--footer);
    color: var(--gray-primary);
    padding: 1rem;
    box-sizing: border-box;
}

.copyright .company b{
    font-size: 1.2rem;
    margin-right: 1rem;
}

.copyright .company span{
    display: block;
}

.copyright .company span:last-of-type{
    font-size: .7rem;
}

.copyright .company .company-icon{
    font-size: 2rem;
    stroke-width: 0;
    stroke: white;
}

.projects{
    display: flex;
    align-items: center;
    justify-content: space-between;
    background-color: var(--primary-dark);
    color: white;
    padding: 0 1rem;
    letter-spacing: 1px;
    font-weight: bold;
    font-family: "Nunito", sans-serif;
}

.company-name{
    font-size: .8rem;
    opacity: .4;
}

.project-list{
    list-style-type: none;
    margin-right: 2rem;
}

.project-list li{
    display: inline-block;
}

.project-list li a{
    color: white;
    text-decoration: none;
    padding: .2rem;
    margin-left: 1rem;
    opacity: .6;
    transition: .2s ease-in-out;
    font-size: .8rem;
}

.project-list li a:hover{
    opacity: .8;
}

