/* this is the styling for the fixed floating post ads button */
.add-post-btn{
    padding: .5rem;
    background: var(--default-light);
    color: var(--primary);
    font-size: 2rem;
    border-radius: var(--circle);
    transition: all .4s ease-out;
    cursor: pointer;
    overflow: hidden;
    margin-right: 1.2rem;
    position: fixed;
    bottom: 70px;
    right: 10px;
    z-index: 1000;
    opacity: .5;
}

.add-post-btn:hover{
    background: var(--secondary-hover);
    color: white;
    transform: scale(1.2);
    opacity: 1;
}

*{
    font-family: "Nunito", sans-serif;
}


/* this is the banner of the landing page styling. both the left and right */
.banner, .banner > *{
    box-sizing: border-box;
}

.banner{
    width: 100%;
    max-height: 600px;
    height: auto;
    background: #f7f6f6;
    padding: 2rem;
    font-family: 'Nunito', sans-serif;
}

.banner-inner{
    height: 100%;
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.sides{
    height: 100%;
    width: 100%;
    padding: 2rem;
}

.left{
    display: flex;
    align-items: center;
}

.left .intro{
    width: 100%;
}

.left .intro h1{
    margin-top: 0;
    opacity: .8;
    font-weight: bolder;
    color: var(--primary);
}

.left .intro h2{
    opacity: .5;
}

.search-label{
    display: flex;
    justify-content: space-between;
    margin-bottom: .5rem;
    opacity: .4;
    font-weight: bold;
}

.call-to-action{
    margin-top: 3rem;
    display: flex;
    align-items: center;
}

.call-to-action .action-btn{
    font-size: .8rem;
    letter-spacing: 1px;
}

.call-to-action .action-btn:first-of-type{
    margin-right: 1rem;
}

.right{
    display: flex;
    align-items: center;
    justify-content: center;
    padding-right: 0;
}

.right img{
    max-height: 100%;
    max-width: 100%;
}

/* this is the styling for the section heading component */
.section-heading{
    color: white;
    text-transform: uppercase;
    position: relative;
    display: inline;
    font-weight: bold;
    font-size: 1.5rem;
    align-items: center;
    animation: move-after .7 linear infinite;
    margin-left: 1rem;
}

.section-heading::before, .section-heading::after{
    position: absolute;
    content: "";
    height: 7px;
    width: 30px;
    bottom: -15px;
}

.section-heading::after{
    background: var(--secondary-hover);
    left: 1px;
    border-radius: 2px 0 0 2px;
    animation: move-after 1s linear infinite;
}

.section-heading::before{
    background: white;
    left: 31px;
    border-radius: 0 2px 2px 0;
    animation: move-before 1s linear infinite;
}

@keyframes move-after{
    0%{
        left: 1px;
    }
    50%{
        left: 31px;
        border-radius: 2px;
    }
    100%{
        left: 1px;
    }
}

@keyframes move-before{
    0%{
        left: 31px;
    }
    50%{
        left: 1px;
        border-radius: 2px;
    }
    100%{
        left: 31px;
    }
}

/* MEDIA RULES BELOW HERE */

@media all and (max-width: 766px){
    .sides.left{
        padding-left: 0 !important;
        padding-right: 3rem !important;
    }

    .sides.right{
        padding-right: 0;
        display: none;
    }
}

@media all and (max-width: 415px){
    .sides.left{
        padding-right: 0 !important;
    }
}

/* ======= Special offer slider section ======= */
.special-offer{
    margin-top: 2rem;
}

.special-offer .slick-arrow{
    color: var(--primary);
    background-color: white;
    padding: .7rem;
    z-index: 5;
    border-radius: 50px;
    box-shadow: 0 0 7px gray;
    transition: .2s ease-in-out;
    font-size: 1.1rem;
    opacity: .4;
}

.special-offer .slick-arrow:hover{
    background-color: #f0f0f0;
    color: var(--primary);
    opacity: 1;
}